"use client"

import { createContext, useContext, useState, useEffect, ReactNode } from 'react'
import { useRouter } from 'next/navigation'

interface User {
  id: string
  phone: string
  email: string
  nativeLanguage: string
  isVerified: boolean
}

interface AppContextType {
  user: User | null
  setUser: (user: User | null) => void
  isLoading: boolean
  login: (phone: string, email?: string) => Promise<{ success: boolean }>
  logout: () => void
  updateLanguage: (language: string) => Promise<void>
}

const AppContext = createContext<AppContextType | undefined>(undefined)

export function AppProvider({ children }: { children: ReactNode }) {
  const router = useRouter()
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Check for existing session
    const checkSession = async () => {
      try {
        // TODO: In production, verify session with backend
        const savedUser = localStorage.getItem('user')
        if (savedUser) {
          setUser(JSON.parse(savedUser))
        }
      } catch (error) {
        console.error('Session check failed:', error)
      } finally {
        setIsLoading(false)
      }
    }

    checkSession()
  }, [])

  const login = async (phone: string, email?: string) => {
    try {
      setIsLoading(true)
      
      // If only phone is provided, create a temporary email
      const userEmail = email || `${phone}@temp.com`
      
      const response = await fetch('/api/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ phone, email: userEmail }),
      })

      if (!response.ok) {
        const data = await response.json()
        throw new Error(data.error || 'Login failed')
      }

      const data = await response.json()
      
      // Create temporary user object
      const newUser: User = {
        id: 'temp-' + Date.now(),
        phone,
        email: userEmail,
        nativeLanguage: '',
        isVerified: false,
      }

      setUser(newUser)
      localStorage.setItem('user', JSON.stringify(newUser))
      
      // Return success to allow the registration page to handle navigation
      return { success: true }
    } catch (error) {
      console.error('Login error:', error)
      throw error
    } finally {
      setIsLoading(false)
    }
  }

  const logout = () => {
    setUser(null)
    localStorage.removeItem('user')
    router.push('/')
  }

  const updateLanguage = async (language: string) => {
    if (!user) return

    try {
      setIsLoading(true)
      
      const response = await fetch('/api/translate', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userId: user.id,
          nativeLanguage: language,
        }),
      })

      if (!response.ok) {
        throw new Error('Failed to update language')
      }

      const updatedUser = {
        ...user,
        nativeLanguage: language,
      }

      setUser(updatedUser)
      localStorage.setItem('user', JSON.stringify(updatedUser))
      
      // Redirect to chat after language is set
      router.push('/chat')
    } catch (error) {
      console.error('Language update error:', error)
      throw error
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <AppContext.Provider
      value={{
        user,
        setUser,
        isLoading,
        login,
        logout,
        updateLanguage,
      }}
    >
      {children}
    </AppContext.Provider>
  )
}

export function useApp() {
  const context = useContext(AppContext)
  if (context === undefined) {
    throw new Error('useApp must be used within an AppProvider')
  }
  return context
}

// Example usage:
/*
// In _app.tsx or layout.tsx:
<AppProvider>
  <Component {...pageProps} />
</AppProvider>

// In any component:
const { user, login, logout, updateLanguage } = useApp()

// Login
await login('+1234567890', 'user@example.com')

// Update language
await updateLanguage('es')

// Access user info
console.log(user?.nativeLanguage)

// Logout
logout()
*/
